export const isIntType = (thingModelType: string) => {
  switch (thingModelType) {
    case 'uint8':
    case 'uint16':
    case 'uint32':
    case 'int':
    case 'int8':
    case 'int16':
    case 'int32':
    case 'int64':
      return true;
      break;
    default:
      return false;
      break;
  }
  return false;
};

export const getMappingType = (thingModelType: string) => {
  switch (thingModelType) {
    case 'uint8':
    case 'uint16':
    case 'uint32':
    case 'int':
    case 'int8':
    case 'int16':
    case 'int32':
    case 'int64':
      return DataType.IntType;
      break;
    case 'float':
    case 'double':
		return DataType.FloatType;
			break;
    default:
      break;
  }
  return thingModelType;
};
export type iBoolProps = iBaseProps;


export enum DataType {
  IntType = 'int',
	FloatType = 'float',
  BoolType = 'bool',
  EnumType = 'enum',
}

export const getComponentsType = (type: string) => {
  switch (type) {
    case 'uint8':
    case 'uint16':
    case 'uint32':
    case 'int':
    case 'int8':
    case 'int16':
    case 'int32':
    case 'int64':
    case 'float':
    case 'double':
      return ComponentsType.NumberType;
      break;
    default:
      break;
  }
  return type;
};
export enum ComponentsType {
  NumberType = 'number',
  BoolType = 'bool',
  EnumType = 'enum',
  WeatherType = 'weather',
  Layout = 'Layout',
  Element = 'Element',
  Custom = 'Custom',
  Consumables = 'Consumables',
	cloudTimer = 'cloudTimer',
}

